import React, {useState} from 'react';
import Submenu from '../Submenu/Submenu';

import style from './BurgerNav.module.css';
import BurgerNavItem from './BurgerNavItem/BurgerNavItem';

let state = {
  accommodation: ['  ', ' ']
}


const BurgerNav = () => {


    return (
        <nav className={style.burgerNav}>         
          
          <BurgerNavItem mainLink='' submenu={state.accommodation} />
          <BurgerNavItem mainLink='' submenu='medicine' />
          <BurgerNavItem mainLink='' submenu='events' />
          <BurgerNavItem mainLink='  ' submenu='beauty' />
          <BurgerNavItem mainLink='  ' submenu='restaurants' />
          <BurgerNavItem mainLink='' submenu='entertainment' />
          <BurgerNavItem mainLink='' submenu='sport' />
          <BurgerNavItem mainLink='' submenu='children' />
          <BurgerNavItem mainLink='' submenu='transport' onMouseEnter="true" /> 
         </nav>
    )
}

export default BurgerNav;