import React from "react";
import { NavLink } from "react-router-dom";
import CardsContainer from "./CardsContainer/CardsContainer";
import style from "./ChooseSection.module.css";

const ChooseSection = () => {

  return (
    <div className={style.chooseSection}>
      <div className={style.wrapper}>
        <h2>  </h2>

        <div className={style.inputsWrapper}>
          <select className={style.select}>
            <option> </option>
            <option></option>
          </select>

          <input type="date" />

          <div className={style.total}>
            <div className={style.totalItems}>
              <span>2 </span>
              <span></span>
              <span> </span>
              <span></span>
              <span>1 </span>
            </div>
            <div className={style.totalIcon}></div>
          </div>

          <button className={style.search}></button>
        </div>

        <div className={style.variants}>
          <div className={style.menuContainer}>
            <div className={style.menu}>
              <NavLink
                to="/"
                className={(navData) =>
                  navData.isActive ? style.active : style.item
                }
              >
                <div></div>
              </NavLink>
              <NavLink
                to="/afisha"
                className={(navData) =>
                  navData.isActive ? style.active : style.item
                }
              >
                <div></div>
              </NavLink>
              <NavLink
                to="/category"
                className={(navData) =>
                  navData.isActive ? style.active : style.item
                }
              >
                <div> </div>
              </NavLink>
            </div>

            
          </div>

          <CardsContainer  />
        </div>
      </div>
    </div>
  );
};

export default ChooseSection;
